* {
  padding: 0;
  font-family: Space Grotesk, Arial, sans-serif;
}
body {
  padding: 0;
  margin: 0;
}
.srchDiv {
  margin: 0;
  background-color: #f2f2f2;
  padding: 30px;
  display: flex;
  justify-content: center;

  > button {
    background-color: #48484a;
    border-color: #48484a;
    color: #f2f2f2;
    padding: 5px 18px;
    cursor: pointer;
    font-weight: 500;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    outline: none;
    border: none;
    &:hover {
      background-color: #6e6e6e;
    }
  }

  > input {
    border: 1px solid #ececed;
    border-radius: 4pt;
    padding: 4pt 8pt;
    color: #48484a;
    font-size: 14px;
    border: none;
    width: 25%;
    border-radius: 4pt 0 0 4pt;
    height: 20pt;
    &:focus {
      border: none;
      outline: none;
    }
  }
}

.contDiv {
  // background-color: rgb(201, 201, 201);
  height: 100vh;
  width: 70%;
  margin: auto;
  display: flex;
  flex-direction: column;
}

.firstBlock {
  min-height: 300px;
  display: flex;
  margin-top: 25px;

  // background-color: #7d5a5a;
}

.text {
  // background-color: #ececed;
  min-width: 200px;
  min-height: 200px;
  height: 100%;
  display: flex;

  justify-content: center;
  width: 50%;
  flex-direction: column;

  > h3 {
    font-weight: bold;
    font-size: 32px;
    margin-top: 1px;
    margin-bottom: 12px;
  }

  > p {
    font-size: 12px;
    font-weight: 600;
    letter-spacing: 1px;
    margin: 0;
  }
}

.date {
  font-size: 13px;
  color: #eb6e4b;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-weight: 200 !important;
}

.degreeDiv {
  display: flex;
  flex-direction: row;
  align-items: center;

  > h4 {
    font-size: 38px;
    font-weight: 400;
    margin: 5px 15px;
  }
}

.miscDiv {
  border-left: 2px solid #eb6e4b;
  display: flex;
  flex-direction: column;
  padding: 5px;
  margin: 7px 0px;
  > div {
    display: flex;
    flex-direction: row;
    padding: 0px 0px;
    > p {
      font-size: 15px;
      margin: 0;
      margin-right: 15px;
      margin-bottom: 5px;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
    }
  }
}

.map {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #6e6e6e;
  min-width: 200px;
  width: 70%;
  border-radius: 3px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  overflow: hidden;
  height: 230px !important;
}

.line {
  width: fit-content !important;
  height: 500px !important;
}

.lineDiv {
  width: 300px;
  overflow: scroll;
}

.wrapOut {
  width: 100%;
  overflow-x: auto;
  overflow-y: hidden;
}

.wrapIn {
  width: 3000px;
  height: 320px;
}

::-webkit-scrollbar {
  display: none;
}

.incorrect {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  margin-top: 25px;
  background-color: #dcdcdc;
  width: 70%;
  border-radius: 15px;
  margin: 50px auto;
  padding: 20px;

  > h2 {
    margin: 10px;
    color: rgb(255, 46, 46);
  }
  > p {
    margin: 5px;
    color: #373737;
  }
}

.loadingDiv {
  min-height: 70vh;
  // background-color: $bg;
  display: flex;
  justify-content: center;
  // border-top: 1px solid gray;
  align-items: center;

  > div {
    // background-color: $bg;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 200px;
    text-align: center;
    color: rgb(255, 24, 24);
  }
}

.heading {
  background-color: #6e6e6e;
  margin: 0;
  color: white;
  letter-spacing: 1px;
  font-weight: 500;
  font-size: 15px;
  text-align: center;
  padding: 10px 0px;

  > a {
    font-weight: bold;
    letter-spacing: 0;
    text-decoration: none;
    color: #fa8463;
  }
}

.mainDiv {
  margin-bottom: 100px;
}
